.region-editor{--col-count: 3;--col-width: calc(100% - 16px / var(--col-count));width:100%;padding:0 8px;grid-auto-flow:row;box-sizing:border-box}.region-editor_disabled{pointer-events:none;opacity:.4}.region-editor__wrapper{display:grid;width:100%;grid-gap:4px;grid-template-columns:repeat(var(--col-count), minmax(0, 1fr))}.region-editor__time-control{display:flex;justify-content:space-around;width:100%}.region-editor__property{height:32px;display:flex;position:relative;align-items:center;justify-self:stretch}.region-editor__text{top:0;left:0;height:100%;display:flex;width:16px;margin-left:8px;align-items:center;position:absolute;text-align:center;opacity:.3;font-size:12px;pointer-events:none}.region-editor__input{margin:0;flex:1;font-size:12px}.region-editor__input:not([type=checkbox],[type=radio]){flex:1;outline:none;width:100%;border:1px solid rgba(0,0,0,0);height:32px;border-radius:4px;box-sizing:border-box;padding:4px 4px 4px 24px;transition-property:background-color,border-color;transition-duration:80ms;transition-timing-function:ease}.region-editor__input:not([type=checkbox],[type=radio]):hover{border-color:var(--sand_300)}.region-editor__input:not([type=checkbox],[type=radio]):focus{border-color:var(--grape_500);box-shadow:0 0 0 1px var(--grape_500)}.region-editor__input[type=checkbox]+.region-editor__text,.region-editor__input[type=radio]+.region-editor__text{margin-left:0;margin-right:4px;position:static}