.toggle{width:42px;height:24px;display:block;min-width:42px;max-height:24px;border-radius:12px;position:relative;cursor:pointer;color:var(--toggle-color, var(--grape_500));transition:box-shadow 80ms ease;background:linear-gradient(0deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)),var(--sand_0);box-shadow:inset 0 1px 0 rgba(0,0,0,.05),inset 0 0 0 1px rgba(0,0,0,.05)}.toggle__input{top:0;left:0;margin:0;opacity:0;padding:0;width:100%;height:100%;position:absolute}.toggle__indicator{height:24px;width:24px;display:flex;align-items:center;justify-content:center;transition:all 120ms ease}.toggle__indicator::before{content:"";width:10px;height:10px;border-radius:100%;transition:all 120ms ease;background:var(--sand_200);box-shadow:0 5px 10px rgba(0,0,0,.15),inset 0 -1px 0 rgba(0,0,0,.1),inset 0 0 0 1px rgba(0,0,0,.05)}.toggle_checked .toggle__indicator{margin-left:18px}.toggle_checked .toggle__indicator::before{width:16px;height:16px;background:currentcolor;box-shadow:0 5px 10px var(--grape_100),inset 0 -1px 0 rgba(0,0,0,.1)}.toggle_disabled{box-shadow:none;pointer-events:none;background:var(--sand_200)}.toggle_disabled .toggle__indicator{transition:none}.toggle_disabled .toggle__indicator::before,.toggle_disabled.toggle_checked .toggle__indicator::before{box-shadow:none;transition:none;background-color:var(--sand_500)}