.video-canvas{width:max-content;height:max-content;position:relative}.video-canvas canvas{display:block}.video-canvas__view canvas{width:100%;height:100%}.video-canvas__toolbar{display:flex;justify-content:space-between}.video-canvas__loading{top:0;left:0;width:100%;height:100%;display:flex;position:absolute;align-items:center;justify-content:center;background-color:var(--sand_100)}.video-canvas__buffering{left:0;right:0;bottom:0;height:4px;font-size:12px;position:absolute;padding:2px 7px;text-align:center;background-color:var(--sand_0);background-image:repeating-linear-gradient(90deg, var(--grape_500) 0, var(--grape_400) 120px, var(--grape_500) 240px);animation:buffering 1.2s linear infinite}.spinner{width:40px;height:40px;border-radius:100%;border:2px solid var(--persimmon_400);border-right-color:rgba(0,0,0,0);border-bottom-color:rgba(0,0,0,0);animation:spin 1.2s linear infinite}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes buffering{0%{background-position:0 0}100%{background-position:240px 0}}