.flyoutmenu{width:32px;height:32px;margin:4px;display:flex;cursor:pointer;position:relative;align-items:center;justify-content:center;user-select:none;z-index:1;background:none;border:none;padding:0;--text-color: var(--sand_500);--text-color-hover: var(--sand_900)}.flyoutmenu:hover{z-index:100}.flyoutmenu__icon{width:32px;height:32px;opacity:.5;color:var(--text-color-hover)}.flyoutmenu__icon svg{width:100%;height:100%}.flyoutmenu__icon.isClicked{background:var(--sand_200);box-shadow:inset 0 1px 0 rgba(0,0,0,.05),inset 0 0 0 1px rgba(0,0,0,.05);border-radius:5px;opacity:1}.flyoutmenu_active .flyoutmenu__icon,.flyoutmenu:hover .flyoutmenu__icon{opacity:1}.flyoutmenu.hovered{z-index:100}.flyoutmenu.hovered .tooltips{right:100%}.flyoutmenu .tooltips{top:50%;right:100vw;font-size:14px;font-weight:500;user-select:none;position:absolute;box-sizing:border-box;margin-right:10px;transform:translate3d(0, -50%, 0);border-radius:5px;background-color:var(--sand_0);box-shadow:0 0 0 1px rgba(0,0,0,.05),0 5px 10px rgba(0,0,0,.1)}@media(width <= 1200px){.flyoutmenu .tooltips{right:1200px}}.flyoutmenu .tooltips__tooltip{font-size:14px;font-weight:500}.flyoutmenu .tooltips__tooltip-body{display:flex;height:40px;align-items:center;white-space:nowrap;padding:0 14px;color:var(--text-color-hover);flex-direction:row-reverse;justify-content:space-between}.flyoutmenu .tooltips__tooltip_controlled{pointer-events:all}.flyoutmenu .tooltips__tooltip__controls{top:50%;right:100%;position:absolute;transform:translate3d(0, -50%, 0)}.flyoutmenu .tooltips__tooltip__controls-body{display:flex;border-radius:5px;background-color:var(--sand_0);box-shadow:0 0 0 1px rgba(0,0,0,.05),0 5px 10px rgba(0,0,0,.1)}.flyoutmenu .tooltips_alignment_right{padding-right:10px;flex-direction:row-reverse}.flyoutmenu .tooltips_alignment_right .tool_tooltip-body{flex-direction:row-reverse}.flyoutmenu .tooltips_alignment_right .tool__controls{padding-right:10px}.flyoutmenu .tooltips_alignment_right .tool__shortcut{margin:0 20px 0 0}.flyoutmenu .tooltips_alignment_right .tool__label{padding-left:8px;padding-right:12px;flex-direction:row-reverse}.flyoutmenu .tooltips_alignment_left{flex-direction:row;padding-left:10px}.flyoutmenu .tooltips_alignment_left .tool_tooltip-body{flex-direction:row}.flyoutmenu .tooltips_alignment_left .tool__controls-body{padding-left:10px}.flyoutmenu .tooltips_alignment_left .tool__shortcut{margin:0 0 0 20px}.flyoutmenu .tooltips_alignment_left .tool__label{padding-left:12px;padding-right:8px;flex-direction:row}.flyoutmenu .tooltips_active .tool__tooltip{display:none}.flyoutmenu .tooltips_active .tool__tooltip-body{color:var(--text-color-hover)}.flyoutmenu .tooltips_active .tool_expanded{border-radius:5px;background-color:var(--sand_100)}.flyoutmenu .tooltips_active .tool_smart{border-radius:5px;background-color:var(--sand_100)}.flyoutmenu .tooltips:hover .tool__tooltip{display:block}.flyoutmenu .tooltips:hover .tool__tooltip-body{color:var(--text-color-hover)}.flyoutmenu .tooltips__shortcut{opacity:.6;display:flex;font-size:12px;font-weight:bold;color:var(--sand_600);margin:0 20px 0 0}.flyoutmenu .tooltips_active .tool__shortcut,.flyoutmenu .tooltips:hover .tool__shortcut{opacity:1}.flyoutmenu .tooltips_active .tool__label,.flyoutmenu .tooltips:hover .tool__label{color:var(--text-color-hover)}.flyoutmenu .tooltips_expanded .flyoutmenu .tooltips_alignment_right .tool__shortcut{margin:0 20px 0 0}.flyoutmenu .tooltips_expanded .flyoutmenu .tooltips_alignment_left .tool__shortcut{margin:0 0 0 20px}.flyoutmenu .tooltips__key{padding:0 4px;height:16px;display:flex;align-items:center;background:var(--sand_0);border-radius:2px;text-transform:uppercase;box-shadow:0 0 0 1px rgba(0,0,0,.1),0 1px 0 rgba(0,0,0,.08);margin-right:4px}.flyoutmenu .tooltips__label{flex:1;display:flex;align-items:center;font-weight:500;justify-content:space-between;color:var(--text-color-hover)}.flyoutmenu .tooltips__smart{display:flex}.flyoutmenu .tooltips_expanded{width:calc(100% - 8px)}.flyoutmenu .tooltips_smart{--text-color: var(--plum_200);--text-color-hover: var(--plum_400)}