.pagination{--button-color: var(--primary_link);--border-radius: 5px;--height: 40px;--font-size: 16px;--line-height: 19px;height:var(--height);display:flex;align-items:center;width:100%;padding:14px 0 0;box-sizing:initial;justify-content:flex-end}.pagination_outline{border-top:1px solid var(--sand_300)}.pagination_align_left{justify-content:flex-start}.pagination_align_right{justify-content:flex-end}.pagination_noPadding{padding:0}.pagination_size_small{--height: 24px;--border-radius: 3px;--font-size: 12px;--line-height: 15px}.pagination_disabled{--button-color: var(--sand_500);background-color:var(--sand_200);pointer-events:none}.pagination__navigation{display:flex;height:100%;align-items:center;border-radius:var(--border-radius, 5px);background:var(--sand_0);box-shadow:inset 0 -1px 0 rgba(0,0,0,.1),inset 0 0 0 1px rgba(0,0,0,.15)}.pagination__divider{width:1px;height:30px;background-color:var(--sand_300)}.pagination_size_small .pagination__divider{height:20px}.pagination__btn{width:37px;height:100%;display:flex;align-items:center;justify-content:center;user-select:none;cursor:pointer}.pagination_size_small .pagination__btn{width:24px}.pagination__btn_disabled{pointer-events:none;--button-color: var(--sand_500)}.pagination__btn_arrow-left,.pagination__btn_arrow-right{position:relative}.pagination__btn_arrow-left::before,.pagination__btn_arrow-left::after,.pagination__btn_arrow-right::before,.pagination__btn_arrow-right::after{content:"";display:block;position:absolute;left:50%;top:50%;width:9px;height:9px;border:2px solid var(--button-color);border-bottom:none;border-right:none}.pagination__btn_arrow-left::after,.pagination__btn_arrow-right::after{display:none}.pagination_size_small .pagination__btn_arrow-left::before,.pagination_size_small .pagination__btn_arrow-left::after,.pagination_size_small .pagination__btn_arrow-right::before,.pagination_size_small .pagination__btn_arrow-right::after{width:5px;height:5px}.pagination__btn_arrow-left::before,.pagination__btn_arrow-left::after{transform:translate3d(-50%, -50%, 0) rotate(-45deg)}.pagination__btn_arrow-right::before,.pagination__btn_arrow-right::after{transform:translate3d(-50%, -50%, 0) rotate(135deg)}.pagination__btn_arrow-left-double::after{display:block;margin-left:7px}.pagination__btn_arrow-right-double::after{display:block;margin-left:-7px}.pagination__input{width:100px;height:calc(100% - 2px);text-align:center;display:flex;align-items:center;justify-content:center;border:1px solid var(--sand_300);border-top:none;border-bottom:none;background:var(--sand_0);margin:1px 0}.pagination_size_small .pagination__input{min-width:70px}.pagination__input input{width:100%;height:calc(100% - 2px);border:none;margin:1px 0;padding:0;outline:none;text-align:center;background:none;font-weight:500;font-size:var(--font-size);line-height:var(--line-height)}.pagination__page-indicator{flex:1;user-select:none;font-weight:500;font-size:var(--font-size);line-height:var(--line-height)}.pagination__page-indicator span{font-weight:normal;opacity:.4}.pagination__page-size{min-width:170px;border:1px solid var(--sand_300);border-radius:5px;position:relative;background:var(--sand_0);margin:0 0 0 16px;box-sizing:border-box}.pagination_size_small .pagination__page-size{padding-left:8px;min-width:130px}.pagination select{height:40px;min-height:40px;font-size:16px;line-height:22px;box-sizing:border-box;padding:0 16px;transition:box-shadow 80ms ease;border:none;outline:none;background:none;min-width:155px}