.timeline-controls{background:var(--sand_0)}.timeline-controls__counter{height:36px;min-width:115px;display:flex;align-items:center;justify-content:center;background:var(--sand_200);border-radius:4px;font-weight:500;font-size:16px;line-height:19px;color:var(--sand_900);padding:0 8px}.timeline-controls__counter span{opacity:.4;padding-left:5px;font-weight:400}.timeline-controls__counter input{width:100%;height:100%;border:none;background:none;padding:0;margin:0;font-size:16px;line-height:19px;text-align:center}.timeline-controls__main-controls{display:flex;max-width:310px;justify-content:space-between}.timeline-controls .button{background:none;width:36px;height:36px}.timeline-controls .button:hover{box-shadow:inset 0 -1px 0 rgba(0,0,0,.1),inset 0 0 0 1px rgba(0,0,0,.15)}.timeline-controls .button:disabled{opacity:.3}.timeline-controls__time{height:36px;display:flex;font-size:16px;border-radius:4px;color:var(--sand_600);box-shadow:inset 0 0 0 1px rgba(0,0,0,.1)}.timeline-controls__time-section{display:flex;height:100%;padding:0 8px;align-items:center}.timeline-controls__time-section span{opacity:.48}.timeline-controls__time-section span::before{content:":"}.timeline-controls__time-section+.timeline-controls__time-section{border-left:1px solid var(--sand_300)}.timeline-time{padding:0 5px;font-size:16px;display:inline-flex;align-items:center;color:var(--sand_500)}.timeline-time span{font-size:11px;min-width:22px;border-radius:2px;text-align:center;margin-left:5px;display:inline-block;color:var(--sand_500);background:var(--sand_200)}