.auto-accept{height:44px;display:flex;align-items:center;box-sizing:border-box;background-color:var(--sand_0);margin-left:16px}.auto-accept__wrapper{width:100%}.auto-accept__wrapper_loading{opacity:.5}.auto-accept__info{font-size:16px}.auto-accept__action{padding:0;margin:0;margin:2px;width:28px;height:28px;display:flex;align-items:center;justify-content:center;border-radius:100%;color:var(--sand_700);background-color:var(--sand_100)}.auto-accept__action_type_accept svg{width:15px;height:10px}.auto-accept__action_type_reject svg{width:12.5px;height:12.5px}.auto-accept__spinner{width:16px;height:16px;border-radius:100%;box-sizing:border-box;border:2px solid var(--persimmon_400);border-right-color:rgba(0,0,0,0);animation:waiting-spin 1s linear infinite}@keyframes waiting-spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}