.comment-form{--primary-action-color: var(--grape_500);--primary-action-padding: 4px;--primary-action-border-radius: 4px;--primary-action-surface-color-hover: var(--grape_0);--text-input-min-height: 40px;--tooltip-text-color: var(--danger_color);display:grid;grid-template-columns:auto var(--text-input-min-height);align-items:center;gap:4px}.comment-form__primary-action{height:100%;width:100%;display:flex;justify-content:center;align-items:flex-start;flex-shrink:0;flex-grow:0}.comment-form__primary-action button{margin-top:4px;display:flex;justify-content:center;align-items:center;flex-shrink:0;flex-grow:0;appearance:none;border:none;background-color:rgba(0,0,0,0);color:var(--primary-action-color);border-radius:var(--primary-action-border-radius)}.comment-form__primary-action button:hover{background-color:var(--primary-action-surface-color-hover)}.comment-form_inline .comment-form__primary-action{grid-row:1/2;grid-column:2/-1}.comment-form_inline .comment-form__primary-action button{padding:0;height:calc(var(--text-input-min-height) - 8px);width:calc(var(--text-input-min-height) - 8px)}.comment-form__tooltipMessage{color:var(--tooltip-text-color);font-size:.9em}