.toolbar{width:40px;background:var(--sand_0);box-shadow:0 0 0 1px rgba(0,0,0,.05),0 5px 10px rgba(0,0,0,.1);border-radius:7px;position:sticky;top:70px;margin-top:50px}.toolbar::before{height:12px;display:block;background-color:var(--sand_100);content:"";border-radius:7px 7px 0 0}.toolbar__group~.toolbar__group{margin-top:4px;border-top:2px solid var(--sand_300)}.toolbar_expanded{width:auto;min-width:210px;display:flex;flex-direction:column}