.keypoints{--hover: transparent;height:24px;display:flex;background-color:var(--hover)}.keypoints__label{left:0;flex:none;width:var(--offset);height:24px;z-index:2;align-items:center;position:sticky;display:flex;padding:0 8px;justify-content:space-between;background:linear-gradient(0deg, var(--hover), var(--hover) 100%),linear-gradient(0deg, #FAFAFA, #FAFAFA 100%);box-shadow:1px 0 0 rgba(0,0,0,.05)}.keypoints__data{display:grid;text-align:right;grid-auto-flow:column;grid-auto-columns:minmax(24px, 1fr)}.keypoints__data-item_faded{opacity:.4}.keypoints__name{font-size:14px;width:min-content;color:var(--color)}.keypoints__keypoints{flex:1;z-index:1;height:100%;position:relative}.keypoints__lifespan{top:50%;height:2px;width:100%;z-index:1;position:absolute;transform:translateY(-50%);background-color:var(--lifespan-color)}.keypoints__lifespan_hidden{opacity:.3}.keypoints_timeline .keypoints__lifespan{height:20px}.keypoints__point{top:50%;width:9px;height:9px;z-index:2;position:absolute;border-radius:2px;transform:translate3d(-50%, -50%, 0) rotate(45deg);background-color:var(--point-color)}.keypoints_timeline .keypoints__point{width:3px;height:20px;transform:translate3d(-100%, -50%, 0)}.keypoints_timeline .keypoints__point:last-child{transform:translate3d(0, -50%, 0)}.keypoints_timeline .keypoints__point:first-child:last-child{width:6px;transform:translate3d(-50%, -50%, 0)}.keypoints__actions{display:flex}.keypoints:hover{--hover: rgb(0 0 0 / 4%)}.keypoints_selected,.keypoints_selected:hover{--hover: rgb(0 0 0 / 8%)}.region-action{margin:1px;margin-left:2px;height:18px;min-width:18px;cursor:pointer;border-radius:3px;background:var(--sand_0);box-shadow:0 1px 0 rgba(0,0,0,.1),0 0 0 1px rgba(0,0,0,.15)}.region-action svg{width:18px;height:18px}.region-action_danger{color:var(--danger_color)}