.annotations-carousel{--carousel-left: 0px;position:relative;height:100%;align-items:center;min-width:0;flex:1}.annotations-carousel__container{display:flex;width:100%;justify-content:space-between;overflow:hidden;height:100%}.annotations-carousel__carosel{flex:1;display:flex;gap:2px;white-space:nowrap;padding-right:70px;position:relative;transform:translateX(calc(-1 * var(--carousel-left)));transition:all .15s ease-in-out 0s}.annotations-carousel__carousel-controls{position:absolute;right:0;white-space:nowrap;top:50%;transform:translateY(-50%);display:flex;gap:4px;padding-right:4px}.annotations-carousel__nav{padding:0;height:28px;width:28px;box-shadow:0 2px 5px 0 rgba(0,0,0,.2)}.annotations-carousel__nav:hover{background:var(--grape_0);box-shadow:0 2px 5px 0 rgba(0,0,0,.2)}.annotations-carousel__nav_disabled{background:var(--sand_100)}.annotations-carousel__nav_disabled svg{opacity:.4}.annotations-carousel__nav_disabled:hover{background:var(--sand_200);filter:none}.annotations-carousel__nav .button:hover{filter:none}.annotations-carousel__nav .button__arrow_left{transform:rotate(-90deg)}.annotations-carousel__nav .button__arrow_right{transform:rotate(90deg)}