.current-task{padding:0 8px 0 16px;display:flex;align-content:center;align-items:center}.current-task_with-history{height:100%;display:grid;width:220px;grid-template:"id buttons" 1fr "id buttons" 1fr/1fr min-content}.current-task__task-id{grid-area:id;color:var(--sand_900);font-size:14px;user-select:text}.current-task__task-id::before{content:"#"}.current-task__task-count{grid-area:counter;color:var(--sand_500);font-size:11px;user-select:none}.current-task__history-controls{display:flex;grid-area:buttons}.current-task__history-controls_newui{gap:8px;margin-left:8px}.current-task__prevnext{width:32px;height:32px;place-self:center center}.current-task__prevnext_newui:hover{background:var(--grape_0)}.current-task__prevnext::before{top:50%;left:50%;content:"";width:10px;height:10px;display:block;position:absolute;border:2px solid var(--sand_900);border-bottom:none;border-right:none;margin-top:-3px;transform:translate3d(-50%, -50%, 0) rotate(225deg)}.current-task__prevnext_next{grid-area:n;transform:rotate(-90deg)}.current-task__prevnext_next.current-task__prevnext_postpone::before{border-color:var(--grape_500)}.current-task__prevnext_prev{grid-area:p;transform:rotate(90deg)}.current-task__prevnext_hidden{display:none}.current-task__prevnext_disabled:hover{background:inherit}.current-task__prevnext_disabled::before{border:2px solid var(--sand_300);border-bottom:none;border-right:none}