.topbar .controls{flex:1;height:100%;display:grid;grid-auto-columns:1fr;grid-auto-flow:column;justify-content:flex-end}.topbar .controls__tooltip-wrapper button{width:100%}.topbar .controls__skipped-info{display:flex;align-items:center;justify-content:center;font-weight:500;font-size:16px}.topbar .controls__skipped-info svg{margin:0 8px 0 4px}.topbar .controls .button{height:100%;font-size:16px;box-shadow:none;border-radius:0}.topbar .action-dialog{height:278px;width:319px;padding:12px 16px;display:flex;flex-direction:column}.topbar .action-dialog__input-title{margin-bottom:4px}.topbar .action-dialog__input{resize:none;flex:1 1 auto;margin-bottom:16px;background-color:var(--sand_0);font-size:16px}.topbar .action-dialog__footer{display:flex;flex-direction:row;justify-content:flex-end}