.region-item{width:100%}.region-item__header{height:32px;display:flex;align-items:center;font-size:14px;line-height:22px;width:100%;padding:0 8px}.region-item__counter{flex:none;width:24px;display:flex;color:var(--sand_500);align-items:center;justify-content:center}.region-item_hidden .region-item__counter,.region-item_hidden .region-item__title,.region-item_hidden .region-item__score,.region-item_hidden .region-item__id{opacity:.4}.region-item_hidden .region-item__desc{background:var(--sand_0)}.region-item__id{display:flex;color:var(--labelColor);width:16px;height:16px}.region-item__id svg{width:100%;height:100%;color:var(--sand_500)}.region-item__toggle{width:24px;flex:0 0 24px;padding:0 !important;text-align:center}.region-item__desc{position:relative;background:var(--sand_100);padding:7px 32px 7px 12px;border-top:1px solid var(--sand_300);border-bottom-left-radius:5px;border-bottom-right-radius:5px;min-height:36px}.region-item__desc_empty{display:none}.region-item__desc_collapsed{height:36px;overflow:hidden}.region-item__collapse{position:absolute;width:24px;height:24px;top:6px;right:8px;opacity:.25;padding:0}.region-item__controls:empty+.region-item__collapse{display:none}.region-item__prediction{width:24px;height:24px;display:flex;align-items:center;justify-content:center}.region-item__prediction svg{width:100%;height:100%;color:var(--plum_400)}