.seeker{width:100%;height:24px;position:relative}.seeker__track{width:100%;height:100%;border-radius:3px;background:var(--sand_200)}.seeker__indicator{top:0;left:0;z-index:1;width:100px;height:24px;max-width:100%;position:absolute;cursor:col-resize;box-sizing:border-box}.seeker__indicator::before{inset:1px;content:"";display:block;position:absolute;border-radius:3px;background:var(--sand_0);box-shadow:0 0 0 1px rgba(0,0,0,.05),0 2px 5px rgba(0,0,0,.2)}.seeker__position{top:0;left:0;width:1px;z-index:2;cursor:grab;height:100%;position:absolute;background-color:var(--sand_900)}.seeker__position::before{inset:0 -5px;content:"";display:block;position:absolute}.seeker__position:hover{border-radius:30px;box-shadow:0 0 0 3px rgba(0,0,0,.2)}.seeker__minimap{top:0;left:0;z-index:3;width:100%;height:100%;position:absolute;pointer-events:none}