.range{--thickness: 6px;display:flex;align-items:center}.range_align_horizontal{min-width:120px;height:28px;padding:0 5px;flex-direction:row}.range_align_vertical{min-height:120px;width:28px;padding:5px 0;flex-direction:column}.range__body{position:relative}.range_align_horizontal .range__body{height:var(--thickness);width:120px;margin:0 8px}.range_align_vertical .range__body{height:120px;width:var(--thickness);margin:8px 0}.range__line{border-radius:8px;background:var(--sand_100)}.range_align_horizontal .range__line{height:var(--thickness);width:100%}.range_align_vertical .range__line{width:var(--thickness);height:100%}.range__indicator{border-radius:8px;position:absolute;background-color:var(--grape_0)}.range_align_horizontal .range__indicator{top:0;height:var(--thickness)}.range_align_vertical .range__indicator{left:0;width:var(--thickness)}.range__range-handle{width:0;height:0;position:absolute}.range__range-handle::before{content:"";width:16px;height:16px;position:absolute;background:var(--grape_500);border-radius:100%;box-shadow:0 5px 10px var(--grape_100),inset 0 -1px 0 rgba(0,0,0,.1);transform:translate3d(-50%, -50%, 0)}.range__range-handle::after{content:"";width:25px;height:25px;position:absolute;transform:translate3d(-50%, -50%, 0)}.range_align_horizontal .range__range-handle{top:50%}.range_align_vertical .range__range-handle{left:50%}.range__icon{width:28px;height:28px;display:flex;align-items:center;justify-content:center}.range__icon svg{max-width:100%;max-height:100%}