.bottombar .controls{flex:1;height:100%;display:grid;grid-auto-columns:1fr;column-gap:8px;grid-auto-flow:column;justify-content:flex-end;padding-right:8px;align-items:center}.bottombar .controls .button{width:160px;height:40px;font-size:16px;line-height:24px}.bottombar .controls .button_look_danger{border-color:var(--sand_300)}.bottombar .controls .submit{border-radius:4px;background:var(--grape_500);box-shadow:0 4px 4px 0 rgba(0,0,0,.25);display:flex;width:160px;height:40px;justify-content:center;align-items:center;color:var(--sand_0);text-align:center;font-size:16px;font-style:normal;font-weight:500;line-height:24px;letter-spacing:.15px;flex-direction:row-reverse;padding:0;cursor:pointer}.bottombar .controls .submit_disabled{color:rgba(255,255,255,.8);background-color:var(--sand_400)}.bottombar .controls .submit__has_icon{justify-content:space-between}.bottombar .controls .submit span{width:100%}.bottombar .controls .submit__icon{display:flex;max-width:40px;height:100%;justify-content:center;align-items:center;align-self:stretch;border-left:1px solid rgba(255,255,255,.16)}.bottombar .controls .submit__icon div{width:100%;height:100%;display:flex;align-items:center;justify-content:center}.bottombar .controls .submit__icon svg{pointer-events:none;transform:rotate(180deg) scale(1.1)}.bottombar .controls__skipped-info{display:flex;align-items:center;justify-content:center;font-weight:500;font-size:16px}.bottombar .controls__skipped-info svg{margin:0 8px 0 4px}.bottombar .action-dialog{height:278px;width:319px;padding:12px 16px;display:flex;flex-direction:column}.bottombar .action-dialog__input-title{margin-bottom:4px}.bottombar .action-dialog__input{resize:none;flex:1 1 auto;margin-bottom:16px;background-color:var(--sand_0);font-size:16px}.bottombar .action-dialog__footer{display:flex;flex-direction:row;justify-content:flex-end}.submit-option{padding:12px 24px;display:flex;align-items:center;align-self:stretch;color:var(--sand_900);text-overflow:ellipsis;white-space:nowrap;font-size:16px;font-style:normal;font-weight:400;line-height:24px;box-shadow:0 1px 3px 1px rgba(38,38,38,.15),0 1px 2px 0 rgba(38,38,38,.3)}.submit-option:hover::before{content:"";z-index:auto;width:calc(100% - 16px);height:calc(100% - 16px);position:absolute;left:0;top:0;margin:8px;border-radius:4px;opacity:.08;background:var(--grape_500);pointer-events:none}