.control-info{position:relative;margin-left:7px;height:14px;width:14px}.control-info__tooltip{transition:opacity .2s ease-out;background:var(--sand_900);padding:4px 16px;width:220px;height:auto;border-radius:4px;color:var(--sand_0);font-size:16px;position:absolute;left:50%;transform:translateX(-50%);visibility:hidden;opacity:0;z-index:2}.control-info:hover .control-info__tooltip{visibility:visible;opacity:1}