.tool{width:32px;height:32px;margin:4px;display:flex;cursor:pointer;position:relative;align-items:center;justify-content:center;user-select:none;z-index:1;background:none;border:none;padding:0;--text-color: var(--sand_500);--text-color-hover: var(--sand_900)}.tool:hover{z-index:100}.tool:hover .tool__tooltip{display:block}.tool:hover .tool__tooltip-body{color:var(--text-color-hover)}.tool__icon{width:32px;height:32px;opacity:.5;color:var(--text-color-hover)}.tool__icon svg{width:100%;height:100%}.tool_active .tool__icon,.tool:hover .tool__icon{opacity:1}.tool_active .tool__label,.tool:hover .tool__label{color:var(--text-color-hover)}.tool_active .tool__shortcut,.tool:hover .tool__shortcut{opacity:1}.tool_active>.tool__icon{background:var(--sand_200);box-shadow:inset 0 1px 0 rgba(0,0,0,.05),inset 0 0 0 1px rgba(0,0,0,.05);border-radius:5px}.tool_smart{--text-color: var(--plum_200);--text-color-hover: var(--plum_400)}.tool_smart .tool_active>.tool__icon{background:var(--plum_200);box-shadow:inset 0 1px 0 var(--plum_0),inset 0 0 0 1px var(--plum_0)}.tool__tooltip{top:50%;display:none;right:100%;font-size:14px;font-weight:500;user-select:none;position:absolute;box-sizing:border-box;padding-right:10px;transform:translate3d(0, -50%, 0);pointer-events:none}.tool__tooltip-body{display:flex;height:40px;align-items:center;border-radius:5px;white-space:nowrap;padding:0 14px;color:var(--text-color);background-color:var(--sand_0);box-shadow:0 0 0 1px rgba(0,0,0,.05),0 5px 10px rgba(0,0,0,.1)}.tool__tooltip_controlled{pointer-events:all}.tool_alignment_right{flex-direction:row-reverse}.tool_alignment_right .tool__tooltip-body{flex-direction:row-reverse}.tool_alignment_right .tool__controls{padding-right:10px}.tool_alignment_right .tool__shortcut{margin:0 20px 0 0}.tool_alignment_right .tool__label{padding-left:8px;padding-right:12px;flex-direction:row-reverse}.tool_alignment_left{flex-direction:row}.tool_alignment_left .tool__tooltip-body{flex-direction:row}.tool_alignment_left .