.annotations-list{min-width:264px;box-sizing:border-box;position:relative}.annotations-list__draft{border-bottom:1px solid var(--sand_300)}.annotations-list__list{top:calc(100% - 1px);left:-1px;right:-1px;position:absolute;background-color:var(--sand_0);box-shadow:0 0 1px rgba(0,0,0,.1),0 2px 8px -2px rgba(0,0,0,.1),0 0 0 1px rgba(0,0,0,.1) inset}.annotations-list__create{border-bottom:1px solid var(--sand_300)}.annotations-list__create,.annotations-list__entity{height:48px;display:flex;padding:0 16px;align-items:center;cursor:pointer}.annotations-list__create:hover,.annotations-list__entity:hover{background-color:var(--grape_0)}.annotations-list__entity_selected{pointer-events:none;background-color:var(--grape_0)}.annotations-list__user{font-size:14px;line-height:16px;width:136px;display:flex}.annotations-list__icons{display:flex;justify-content:flex-end;align-items:center}.annotations-list__icon-column{display:flex;align-items:center;justify-content:center}.annotations-list__icon-column:nth-child(2){width:32px;height:32px}.annotations-list__name{flex:1;white-space:nowrap;text-overflow:ellipsis;overflow:hidden}.annotations-list__entity-id{font-size:11px;opacity:.5;flex:none;padding-left:5px}.annotations-list__review{font-size:11px;line-height:13px}.annotations-list__review_state_accepted{color:var(--success_color)}.annotations-list__review_state_fixed{color:var(--persimmon_400)}.annotations-list__review_state_rejected{color:var(--danger_color)}.annotations-list__created{font-size:11px;line-height:13px;color:var(--sand_500)}.annotations-list__date{color:var(--sand_500)}.annotations-list__counter{font-size:11px;line-height:13px;color:var(--sand_500)}.annotations-list__toggle{width:32px;height:32px;position:relative}.annotations-list__toggle::before{top:50%;left:50%;content:"";width:10px;height:10px;display:block;position:absolute;border:2px solid var(--sand_900);border-bottom:none;border-right:none;margin-top:-3px;transform:translate3d(-50%, -50%, 0) rotate(225deg)}.annotations-list__toggle_opened::before{margin-top:3px;transform:translate3d(-50%, -50%, 0) rotate(45deg)}