.detailed-region__icon{width:24px;height:24px;margin-right:8px;flex-shrink:0}.detailed-region__head{display:flex;margin:8px 0;padding:0 8px;align-items:center;justify-content:space-between;gap:8px}.detailed-region__title{display:flex;align-items:flex-start;word-break:break-word}.detailed-region__title+span{color:var(--sand_900)}.detailed-region__title+span::before{opacity:.6;content:"ID "}.detailed-region__result{padding:0 8px}.detailed-region__meta-text{width:calc(100% - 16px);border:1px solid rgba(0,0,0,0);margin:0 8px;padding:4px 8px;min-height:64px;border-radius:4px;outline:none;background-color:var(--sand_100);transition-property:background-color,border-color;transition-duration:80ms;transition-timing-function:ease}.detailed-region__meta-text:hover textarea{border-color:var(--sand_300);background-color:var(--sand_0)}.detailed-region__meta-text:focus{background-color:var(--sand_0);border-color:var(--grape_500);box-shadow:0 0 0 1px var(--grape_500)}.detailed-region__meta-text:not(textarea){white-space:pre-wrap;word-break:break-all}.detailed-region_compact .detailed-region__head{margin:-5px 0}.detailed-region__warning{box-sizing:border-box;display:flex;flex-direction:row;align-items:center;padding:8px;gap:8px;background:var(--canteloupe_0);border:1px solid var(--canteloupe_400);border-radius:4px;margin:0 16px 8px;flex:none;order:0;flex-grow:1}.detailed-region__warning svg{width:20px;height:17px;fill:var(--incomplete-warning-color, var(--canteloupe_500))}.region-meta__content{margin:8px 0}.region-meta__content_type_text{padding:3px 6px;border-radius:4px;background-color:var(--sand_100);white-space:pre-wrap;line-break:loose;word-break:break-all}.region-meta__result{display:grid;grid-template-columns:auto 1fr;grid-column-gap:10px}.region-meta__value p{padding:0;margin-bottom:4px}.region-meta__value p::before{padding-right:4px;display:inline-block;content:attr(data-counter);opacity:.6}