.annotation-tabs{height:44px;width:100%;display:flex;display:grid;z-index:100;position:sticky;grid-template-columns:1fr;top:var(--sticky-items-offset);background-color:var(--main-bg-color);border-bottom:1px solid var(--sand_300)}.annotation-tabs_viewAll{grid-template-columns:1fr 44px}.annotation-tabs_addNew{grid-template-columns:44px 1fr}.annotation-tabs_viewAll.annotation-tabs_addNew{grid-template-columns:44px 1fr 44px}.annotation-tabs__add,.annotation-tabs__all{border:none;width:44px;height:44px;padding:0;margin:0;cursor:pointer;font-size:16px;background:none;display:flex;align-items:center;justify-content:center}.annotation-tabs__add{border-right:1px solid var(--sand_300)}.annotation-tabs__all{outline:none;color:var(--sand_600)}.annotation-tabs__all_active{color:var(--grape_500)}.annotation-tabs__list{flex:1;display:flex;overflow:auto;position:relative;-ms-overflow-style:none;scrollbar-width:none}.annotation-tabs__list::-webkit-scrollbar{display:none}.entity-tab{height:100%;min-width:170px;display:flex;cursor:pointer;align-items:center;padding:10px 12px;box-sizing:border-box}.entity-tab_bordered{border-right:1px solid var(--sand_300)}.entity-tab_selected{opacity:1;cursor:default;background-color:var(--main-bg-color);box-shadow:inset 0 2px 0 var(--grape_500)}.entity-tab__userpic{width:24px;height:24px}.entity-tab__userpic_prediction{color:var(--plum_400);background-color:var(--plum_0);box-shadow:inset 0 0 0 1px rgba(148,75,255,.15)}.entity-tab__ground-truth{color:var(--canteloupe_400);margin-top:-2px}.entity-tab__ground-truth path{fill-opacity:1;stroke-opacity:1}.entity-tab__skipped{color:var(--danger_color)}