.audio-slider{--background-active-color: var(--grape_100);--button-color: var(--primary_link);position:relative}.audio-slider__range{margin:0 20px;width:calc(100% - 40px);padding:0;appearance:none;height:4px;background:var(--sand_200);border-radius:8px;background-image:linear-gradient(var(--background-active-color), var(--background-active-color));background-size:70% 100%;background-repeat:no-repeat;border:none}.audio-slider__control{padding:0 16px;margin-top:10px;display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;font-size:11px}.audio-slider__info{display:flex}.audio-slider__input{width:40px;height:24px;border-radius:2px;background:var(--sand_100);border:1px solid var(--sand_300);line-height:24px;padding:0;text-align:center;outline:none}.audio-slider__input_error_control{color:var(--danger_color);border:1px solid var(--red_1)}.audio-slider input[type=range]::-webkit-slider-thumb{appearance:none;height:12px;width:12px;border-radius:50%;background:var(--button-color);cursor:ew-resize;transition:background .3s ease-in-out;z-index:2}.audio-slider input[type=range]::-moz-range-thumb{appearance:none;height:12px;width:12px;border-radius:50%;background:var(--button-color);cursor:ew-resize;box-shadow:0 0 2px 0 var(--sand_700);transition:background .3s ease-in-out}.audio-slider input[type=range]::-ms-thumb{appearance:none;height:12px;width:12px;border-radius:50%;background:var(--button-color);cursor:ew-resize;box-shadow:0 0 2px 0 var(--sand_700);transition:background .3s ease-in-out}.audio-slider input[type=range]::-webkit-slider-thumb:hover{background:var(--button-color)}.audio-slider input[type=range]::-moz-range-thumb:hover{background:var(--button-color)}.audio-slider input[type=range]::-ms-thumb:hover{background:var(--button-color)}.audio-slider input[type=range]::-webkit-slider-runnable-track{appearance:none;box-shadow:none;border:none;background:rgba(0,0,0,0)}.audio-slider input[type=range]::-moz-range-track{appearance:none;box-shadow:none;border:none;background:rgba(0,0,0,0)}.audio-slider input[type=range]::-ms-track{appearance:none;box-shadow:none;border:none;background:rgba(0,0,0,0)}