.radio-group{--radius: 8px;--height: 40px;--padding: 4px;--font-size: 16px;--button-padding: 0 10px;--button-checked-shadow: 0px 1px 0px rgb(0 0 0 / 10%), 0px 0px 0px 1px rgb(0 0 0 / 2%), 0px 5px 10px rgb(0 0 0 / 15%);height:var(--height);border-radius:var(--radius);padding:var(--padding);background:linear-gradient(0deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)),var(--sand_0);box-shadow:inset 0 1px 0 rgba(0,0,0,.05),inset 0 0 0 1px rgba(0,0,0,.05)}.radio-group__buttons{height:calc(var(--height) - var(--padding)*2);display:grid;grid-auto-columns:1fr;grid-auto-flow:column}.radio-group__button{display:flex;opacity:.6;padding:var(--button-padding);font-weight:500;position:relative;text-align:center;align-items:center;justify-content:center;font-size:var(--font-size);border-radius:calc(var(--radius) - var(--padding));height:calc(var(--height) - var(--padding)*2);cursor:pointer}.radio-group__button_checked{opacity:1;background-color:var(--sand_0);box-shadow:var(--button-checked-shadow)}.radio-group__button_disabled{opacity:.3;cursor:not-allowed}.radio-group__input{top:0;left:0;opacity:0;width:100%;height:100%;position:absolute}.radio-group_size_medium{--height: 32px;--radius: 8px;--font-size: 14px}.radio-group_size_small{--height: 24px;--radius: 4px;--padding: 2px;--font-size: 12px;--button-padding: 0 5px;--button-checked-shadow: 0px 1px 0px rgb(0 0 0 / 10%), 0px 0px 0px 1px rgb(0 0 0 / 2%), 0px 2px 4px rgb(0 0 0 / 15%)}