.audio-config{position:relative}.audio-config__modal{left:0;top:36px;position:absolute;width:232px;background:var(--sand_0);border-radius:4px;padding:22px 0 0;box-shadow:0 4px 10px 0 var(--sand_300);z-index:10}.audio-config__range{margin:0 20px;width:calc(100% - 40px);padding:0}.audio-config__buttons{margin-top:10px;padding:2px 0;border-top:1px solid var(--sand_200);font-size:16px;cursor:pointer}.audio-config__menu-button{padding:8px 16px;cursor:pointer}.audio-config__menu-button:hover{background:var(--sand_200)}