.annotation-history{margin:0 0 8px}.annotation-history_inline{padding:0 8px}.annotation-history__title{margin-bottom:8px}.annotation-history__divider{height:1px;position:relative;margin:14px 0;background-color:var(--sand_200)}.annotation-history__divider::before{top:0;left:0;font-size:12px;color:var(--sand_500);padding-right:5px;position:absolute;content:attr(title);transform:translateY(-50%);background-color:var(--sand_0)}.history-item{margin:0;padding:8px;cursor:default;border-radius:5px}.history-item__date{color:var(--sand_700)}.history-item:not(.history-item_disabled){cursor:pointer}.history-item:hover:not(.history-item_disabled){background-color:var(--sand_100)}.history-item_selected:not(.history-item_disabled),.history-item_selected:hover:not(.history-item_disabled){background-color:var(--sand_200)}.history-item__name,.history-item__date{font-size:14px}.history-item__name{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.history-item_disabled{opacity:.6;border-radius:5px;background-color:var(--sand_200)}.history-item__userpic{width:24px;height:24px}.history-item__userpic_prediction{color:var(--plum_500);background-color:var(--plum_100);box-shadow:inset 0 0 0 1px rgba(148,75,255,.15)}.history-item__comment{padding-top:2px}.history-item__comment-content{white-space:pre-wrap;overflow-wrap:anywhere}.history-item__comment-content::before{content:attr(data-reason)}.history-item__comment_collapsed .history-item__comment-content{display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;overflow:hidden;text-overflow:ellipsis}.history-item__history-icon{width:24px;height:24px}.history-item__action{padding-top:6px;align-items:flex-start;grid-template-columns:24px 1fr}.history-item__spin{width:10px;height:10px;margin-right:4px;display:inline-block;border:2px solid var(--grape_500);border-radius:100%;border-top-color:var(--grape_400);animation:spin 1s linear infinite}.history-item__dot{width:10px;height:10px;margin-right:4px;display:inline-block;background-color:var(--sand_400);border-radius:100%;animation:pulse 1s linear infinite}.history-item__saved{animation:disappear .3s 3s forwards;width:12px;margin-right:4px;color:var(--success_color)}.history-item__collapse-comment{margin-top:3px;color:var(--grape_500)}.history-item:not(.history-item_inline){margin:0 -10px}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes pulse{0%{transform:scale(1)}50%{transform:scale(0.5)}}@keyframes disappear{0%{opacity:1}100%{opacity:0}}