.label{--color: var(--sand_500);--background: var(--sand_200);display:inline-flex;padding:2px 8px;margin:0;border-radius:3px;font-weight:400;font-size:14px;line-height:22px;background-color:var(--background);color:var(--sand_900);border-left:4px solid var(--color);position:relative;align-items:center}.label_margins{margin:0 8px 8px 0}.label_margins:last-child{margin:0 0 8px}.label_empty{border-left-width:0}.label_hidden{display:none}.label_clickable{cursor:pointer}.label_solid{background-color:var(--color)}.label_selected{background-color:var(--color);color:var(--white)}.label_selected .label__hotkey{color:var(--white)}.label__text{position:relative;white-space:nowrap}.label__hotkey{position:relative;color:var(--sand_500);font-size:13px;margin-left:12px}