.audio-control{position:relative}.audio-control__modal{left:0;top:36px;position:absolute;width:232px;background:var(--sand_0);border-radius:4px;padding:22px 0 0;box-shadow:0 4px 10px 0 var(--sand_300);z-index:10}.audio-control__range{margin:0 20px;width:calc(100% - 40px);padding:0}.audio-control__mute{margin-top:10px;padding:2px 0;border-top:1px solid var(--sand_200);font-size:16px;cursor:pointer}.audio-control__mute-button{padding:8px 16px}.audio-control__mute-button:hover{background:var(--sand_200)}