.link-state{display:flex;flex-direction:row;align-items:center;gap:4px;font-size:13px;line-height:1.2em;transition:color .2s ease;padding:6px 4px 5px}.link-state_display{padding:2px 9px 5px 4px;gap:2px;font-size:14px;line-height:22px}.link-state__prefix{flex-grow:0;display:flex;flex-flow:row;align-items:center}.link-state_action .link-state__prefix{color:var(--primary-action-color)}.link-state_display .link-state__prefix{color:var(--secondary-action-color-hover)}.link-state__prefix svg{width:20px;height:20px}.link-state-region{display:flex;flex-flow:row nowrap;align-items:center;gap:4px;font-size:1em;line-height:1.2em;justify-content:stretch;width:100%}.link-state-region__icon{display:flex;flex:none;max-width:24px;width:min-content;align-items:center;justify-content:center;color:var(--icon-color);flex-grow:0}.link-state-region__icon svg{width:24px;height:24px}.link-state-region__index{width:18px;height:18px;display:flex;font-size:9px;color:var(--sand_0);text-align:center;align-items:center;border-radius:4px;justify-content:center;background-color:var(--text-color, var(--sand_900));flex-grow:0}.link-state-region__title{align-items:center;display:inline-flex;flex:1;color:var(--text-color, var(--sand_900));min-width:0}.link-state-region__close{flex-grow:0}