.sidebar-tabs{flex:1;position:sticky;display:flex;flex-direction:column;max-height:100vh;top:var(--sticky-items-offset)}.sidebar-tabs__toggle{width:100%;height:44px;display:flex;background-color:var(--sand_200)}.sidebar-tabs__tab{flex:1;padding:12px 16px;font-weight:500;font-size:16px;line-height:19px;cursor:pointer;box-sizing:border-box;box-shadow:0 1px 0 rgba(0,0,0,.1)}.sidebar-tabs__tab:not(:last-child){box-shadow:-1px 0 0 rgba(0,0,0,.1) inset,0 1px 0 rgba(0,0,0,.1)}.sidebar-tabs__tab_active{background-color:var(--sand_0);box-shadow:none;cursor:default}.sidebar-tabs__tab_active:not(:last-child){box-shadow:-1px 0 0 rgba(0,0,0,.1) inset}.sidebar-tabs__content{flex:1;overflow:auto}